<template>
  <div id="app">
    <div class="container">
      <div class="left">
        <el-upload
                class="upload-demo"
                drag
                :action="actionURL"
                :show-file-list="false"
                :on-success="onSuccess"
                :before-upload="beforeUpload"
                ref="upload"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        </el-upload>
        <div class="thumbnail" v-if="thumbnail">
          <img :src="thumbnail"/>
        </div>
        <el-button
                class="recognize-btn"
                type="primary"
                size="large"
                :loading=loading
                @click="recognizeImage"
        >
          识别
        </el-button>
      </div>
      <div class="right" v-show="result !== ''">
        <div class="result" v-html="result"></div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                imageFile: null,
                result: "",
                thumbnail: "",
                actionURL:
                // "http://127.0.0.1:4523/m1/2220283-0-default/api/tr-run",
                    "http://localhost:8080/api/tr-run/",
                loading: false,
            };
        },
        methods: {
            beforeUpload(file) {
                this.imageFile = file;
                const reader = new FileReader();
                reader.onload = (e) => {
                    this.thumbnail = e.target.result;
                };
                reader.readAsDataURL(file);
            },
            async recognizeImage() {
                if (!this.imageFile) {
                    this.$message.error("请先上传一张图片");
                    return;
                }
                this.loading = true;
                const formData = new FormData();
                formData.append("file", this.imageFile);
                try {
                    const response = await fetch(this.actionURL, {
                        method: "POST",
                        body: formData,
                    });
                    const data = await response.json();
                    console.log(data);
                    this.result = data.data;
                    this.loading = false;
                } catch (error) {
                    this.$message.error("识别失败，请重试");
                    this.loading = false;
                }
            },
            onSuccess(response, file, fileList) {
                this.$message({
                    message: "上传成功",
                    type: "success",
                });
            },
        },
    };
</script>

